let setNumOnlyInput;

setNumOnlyInput = (target, maxleng, additional) => {

    // ------------------------------------------------------------ //
    // e.keyCode 
    // 48~57:   0~9
    // 96~105:    0~9
    // 8: backspace, 9: tab, 46: delete, 13: enter,
    // 37: ArrowLeft, 38: ArrowUp, 39: ArrowRight, 40: ArrowDown
    // 299:  Process
    // ------------------------------------------------------------ //

    target.addEventListener('keydown', (e) => {
        let value, isNumKey, isSelected, isNecessary;
        value = e.target.value;
        isNumKey = (e.keyCode >= 48 && e.keyCode <= 57) || (e.keyCode >= 96 && e.keyCode <= 105); // ?
        isSelected = target.selectionStart === 0 && target.selectionEnd === 2 // ?
        isNecessary = e.keyCode === 8 || e.keyCode === 9 || e.keyCode === 13 || e.keyCode === 46 || (e.keyCode >= 37 && e.keyCode <= 40); //  key(backspace,  )?

        if (isNumKey) {
            if (value.length >= maxleng && !isSelected) { //  maxleng ,     
                e.preventDefault();
                e.target.value = value;
            }
        } else if (!isNecessary) {
            e.preventDefault();
            e.target.value = value;
        } else {
            if (additional) additional(e);
        }
    }, false);

    target.addEventListener('input', (e) => {
        e.target.value = e.target.value.replace(/[\--]/g, ""); //  /  '' 
    }, false);
}

export default setNumOnlyInput;